/* roboto-condensed-regular - latin_latin-ext */
@font-face {
  font-family: 'Roboto Condensed';
  font-style: normal;
  font-weight: 400;
  src: url('/assets/roboto-condensed-v16-latin_latin-ext-regular.eot');
  /* IE9 Compat Modes */
  src: local('Roboto Condensed'), local('RobotoCondensed-Regular'),
    url('/assets/roboto-condensed-v16-latin_latin-ext-regular.eot?#iefix') format('embedded-opentype'),
    /* IE6-IE8 */
    url('/assets/roboto-condensed-v16-latin_latin-ext-regular.woff2') format('woff2'),
    /* Super Modern Browsers */
    url('/assets/roboto-condensed-v16-latin_latin-ext-regular.woff') format('woff'),
    /* Modern Browsers */
    url('/assets/roboto-condensed-v16-latin_latin-ext-regular.ttf') format('truetype'),
    /* Safari, Android, iOS */
    url('/assets/roboto-condensed-v16-latin_latin-ext-regular.svg#RobotoCondensed') format('svg');
  /* Legacy iOS */
}

